export const getImageCode = data => q({
  url: '/Api/Common/VerifyCode/getImageCode',
  method: 'POST',
  data
})

export const checkVerifyCode = data => q({
  url: '/Api/Common/VerifyCode/checkVerifyCodeNew',
  method: 'POST',
  data
})

export const sendCode = data => q({
  url: '/Api/Common/VerifyCode/sendCode',
  method: 'POST',
  data
})

export const uploadBase64Img = data => q({
  url: '/Api/User/UploadFile/uploadBase64Img',
  method: 'POST',
  data
})

export const uploadVideo = data => q({
  url: '/Api/User/UploadFile/uploadVideo',
  method: 'POST',
  data,
})

export const uploadLargeVideo = (data, opts = {}) => q({
  url: '/Api/User/UploadFile/uploadLargeVideo',
  method: 'POST',
  data,
  opts
})

export const uploadFileToken = data => q({
  url: '/Api/User/UploadFile/uploadFileToken',
  method: 'POST',
  data,
})

