<template>
  <div class="page-sign-in">
    <van-form validate-first @submit="onSubmit">
      <!--      用户名注册-->
      <van-field v-model="form.userName" autocomplete="new-password" :left-icon="icon"
                 maxlength="20" minlength="6"
                 :placeholder="placeholder"
                 type="text">
      </van-field>

      <div v-if="slideVerify" style="display:flex;justify-content:center;">
        <div ref="dx"></div>
      </div>

      <van-field v-else v-model="form.imgCode" autocomplete="new-password" left-icon="youzan-shield"
                 placeholder="验证码">
        <template #extra>
          <van-image :src="verifyImageBase64" class="verifyCode" @click="refreshImageVerifyCode"/>
        </template>
      </van-field>

      <van-field v-if="mode === 2" autocomplete="new-password"
                 v-model="form.smsCode"
                 placeholder="短信验证码"
                 left-icon="youzan-shield">
        <template #extra>
          <van-button class="btnGetVerifyCode primary" size="small" native-type="button"
                      @click="getSmsVerifyCode">
            <van-count-down v-if="smsCountdown" :time="smsCountdown" format="ss" auto-start
                            @finish="smsCountdown = 0"/>
            <template v-else> 获取验证码</template>
          </van-button>
        </template>
      </van-field>

      <template v-if="mode === 1 || mode === 2">
        <van-field v-model="form.password" :right-icon="showPassword ? 'eye-o' : 'closed-eye'"
                   :type="showPassword ? 'text' : $root.dynamicPasswordInputType"
                   autocomplete="new-password" left-icon="lock"
                   placeholder="6-15位字母数字，且不能全为字母或数字"
                   @click-right-icon="showPassword = !showPassword">
        </van-field>

        <van-field v-model="form.confirmPassword"
                   :right-icon="showConfirmPassword ? 'eye-o' : 'closed-eye'"
                   :type="showConfirmPassword ? 'text' : $root.dynamicPasswordInputType"
                   autocomplete="new-password" left-icon="lock"
                   placeholder="6-15位字母数字，且不能全为字母或数字"
                   @click-right-icon="showConfirmPassword = !showConfirmPassword">
        </van-field>
      </template>

      <div class="ver">版本号：V {{ $root.version }}</div>

      <!--      <van-button :key="1" v-if="mode === 2 && !passedImageVerifyCode" class="primary block"-->
      <!--                  native-type="button"-->
      <!--                  @click="nextStep">-->
      <!--        下一步-->
      <!--      </van-button>-->
      <van-button :key="2" v-if="mode === 1 || mode === 2"
                  class="primary block" native-type="submit">
        注册
      </van-button>
    </van-form>

    <van-dialog v-model.lazy="showDialog" class="signProtocol" confirm-button-text="我已阅读"
                title="注册须知">
      {{$root.title}}是一个专业的支付通道，基于C2C理念
      开发设计，全新的UI让会员耳目一新，全新的
      认证系统让真实的会员更有安全感，让骗单玩 家无处遁形，
      一旦发现骗单，平台将永久冻结账户，永不
      解封。最大程度的提升了会员与商户之间的
      用户粘度，构建商户与会员之间的紧密链接，
      {{$root.title}}将竭诚为广大客户提供最优质的服务。
      <br/>
      交易过程中禁止添加好友 如果添加好友造成被骗单平台概不承担责任。
      <br/>
      交易流程示范 注册-购买V币-到商户平台充值-提款下分
      V币-到我要卖币挂单等待买家购买-买家付
      款-卖家确认-完成交易。在这整个交易过程
      当中，还有一些注意事项，请您务必阅读。
      <br/>
      第一： 为了保证会员之间的交易安全，平台采取的是
      实名制用户认证，并终身无法修改认证信息！
      会员必须遵守规定，严格启用本人身份信息，
      银行账号信息等；买家付款时必须使用本人身
      份信息的银行卡或者其支付工具来购买，如果
      使用他人银行账户代为付款，卖家有权可以不
      予确认收款。由于本人录入信息不符而造成的
      一切损失，由会员自行承担，平台不负担任何 责任；
      <br/>
      第二： 为了保证卖家的信息安全，请买家务必在有需
      求的时候再进行订单确认，如若发生恶意刷
      单，锁定卖家额度而不付款的用户，平台将会 自动冻结账户；
      <br/>
      第三： 在购买以及出售V币的交易时间，平台规定
      是必须在15分钟内完成，会员在购买或者出售
      的时候，一定要非常关注交易是否在正在进行
      当中，如有异议也及时的联系平台客服，申请
      人工客服介入！由于买卖双方长时间不确认的
      交易，系统将自动判定为成交或者取消，由于
      系统无法做到百分百准确，平台不承担任何责
      任。再次重申，请双方在交易的过程当中要时 刻注意订单变化！
      <br/>
      第四： 『买家』在购买V币的时候，此单：一旦付款
      成功．请立即上传截图并点击『我己转账』确认，
      确认以后10分钟内如果还没有收到币，此单便
      为争议订单，请立即联系在线客服，否则系统将 判断定您的付款无效，取消订单！
      <br/>
      第五： 会员在出售V币的时候，此订单一旦挂单成
      功，如有『买家』下单购买并点击了『我已转
      账』请您马上查询是否收到汇款，如没有收到
      『买家』的转账，请立即点击『未收到转账』
      功能，并联系在线客服否则在10分钟后，系统
      会判断为此订单交易成功，系统将会自动打币 给『买家』；
    </van-dialog>
  </div>
</template>

<script>
import {register} from "@/apis/user";
import {getImageCode, sendCode} from "@/apis/verify";

export default {
  data() {
    return {
      passedImageVerifyCode: false,
      showDialog: true,
      form: {
        userName: "",
        // phoneNumber: "",
        imgCode: "",
        smsCode: "",
        password: "",
        confirmPassword: "",
        verifyToken: "",
      },
      verifyImageBase64: "",
      smsCountdown: 0,
      showPassword: false,
      showConfirmPassword: false,
      verifyUniqid: "",
    };
  },
  computed: {
    mode() {
      return Number(this.$root.registerMode)
    },
    placeholder() {
      if (this.mode === 1) {
        return '用户名'
      }
      if (this.mode === 2) {
        return '手机号'
      }
    },
    icon() {
      if (this.mode === 1) {
        return 'manager'
      }
      if (this.mode === 2) {
        return 'phone'
      }
    },
    graphicVerify() {
      return Number(this.$root.verifyCodeType) === 1
    },
    slideVerify() {
      return Number(this.$root.verifyCodeType) === 2
    }
  },
  created() {
    this.refreshImageVerifyCode();
  },
  methods: {
    onSubmit() {
      if (this.mode === 1) {
        if (!/^.{6,20}$/.test(this.form.userName))
          return this.$toast.fail("请输入6-20位用户名");
      }
      if (this.mode === 2) {
        if (!/^1[3456789]\d{9}$/.test(this.form.userName))
          return this.$toast.fail("请输入合法的手机号码");

      }
      if (this.slideVerify && !this.form.verifyToken) {
        return this.$toast.fail("请滑动验证");
      }
      if (this.graphicVerify && this.form.imgCode.length <= 0) {
        this.refreshImageVerifyCode();
        return this.$toast.fail("请输入图片验证码");
      }
      if (this.mode === 2) {
        if (this.form.smsCode.length !== 4)
          return this.$toast.fail("请输入4位短信验证码");
      }
      if (
        !/^(?![^a-zA-Z]+$)(?!\D+$)/.test(this.form.password) ||
        this.form.password.length < 6 ||
        this.form.password.length > 15
      )
        return this.$toast.fail("请输入6-15位字母数字组成的密码");
      if (this.form.password !== this.form.confirmPassword)
        return this.$toast.fail("两次输入的密码不一致");


      const refreshVerifiedImage = () => {
        this.refreshImageVerifyCode();
        this.dx && this.dx.reload()
        this.form.verifyToken = "";
        this.form.imgCode = "";

      }

      register({
        smsCode: this.form.smsCode,
        userName: this.form.userName,
        password: this.form.password,
        code: this.form.imgCode,
        verifyUniqid: this.verifyUniqid,
        verifyToken: this.form.verifyToken
      }).then(({data, ok, msg}) => {
        if (ok) {
          this.$router.replace("/layout_auth");
          this.$toast.success(msg);
        } else {
          this.$toast.fail(msg);
          refreshVerifiedImage()
        }
      });

    },
    // nextStep() {
    //   if (!/^1[3456789]\d{9}$/.test(this.form.userName))
    //     return this.$toast.fail("请输入合法的手机号码");
    //   if (this.slideVerify && !this.form.verifyToken) {
    //     return this.$toast.fail("请滑动验证");
    //   }
    //   if (this.graphicVerify && this.form.imgCode.length <= 0) {
    //     this.refreshImageVerifyCode();
    //     return this.$toast.fail("请输入图片验证码");
    //   }
    //
    //   checkVerifyCode({
    //     userName: this.form.userName,
    //     code: this.form.imgCode,
    //     codeType: "register",
    //     verifyUniqid: this.verifyUniqid,
    //     verifyToken: this.form.verifyToken,
    //   }).then(({ok, msg}) => {
    //     if (ok) {
    //       this.passedImageVerifyCode = true;
    //     } else {
    //       this.refreshImageVerifyCode();
    //       this.dx && this.dx.reload()
    //       this.form.verifyToken = "";
    //       this.form.imgCode = "";
    //       this.$toast.fail(msg);
    //     }
    //   });
    // },
    refreshImageVerifyCode() {
      getImageCode({
        codeType: "register",
      }).then(({ok, data, code}) => {
        if (ok) {
          this.verifyUniqid = data.verifyUniqid;
          this.verifyImageBase64 = data.verifyCode;
        } else {
          if (code === 400) return this.$toast.fail("请输入合法的手机号码");
        }
      });
    },
    getSmsVerifyCode() {
      if (this.smsCountdown) {
        return;
      }
      sendCode({
        phoneNumber: this.form.userName,
        codeType: "register",
      }).then(({ok, msg, data}) => {
        if (process.env.NODE_ENV === "development") {
          this.form.smsCode = data;
        }
        if (ok) {
          this.$toast.success(msg);
          this.smsCountdown = 60000;
          this.$st.set("LAST_SMS_REQUEST", Date.now());
        } else {
          this.$toast.fail(msg);
        }
      });
    },
  },
  mounted() {
    const _that = this;
    const dx = this.$refs.dx
    if (dx) {
      _dx.Captcha(dx, {
        // appId, 在控制台应用管理或应用配置模块获取
        appId: process.env.VUE_APP_DX_APPID,
        apiServer: process.env.VUE_APP_DX_API_SERVER,
        style: "inline",
        // apiServer域名地址在控制台页面->无感验证->应用管页面左上角获取，必须填写完整包括https://。
        success: token => {
          // 获取验证码token，用于后端校验，注意获取token若是sl开头的字符串，则是前端网络不通生成的降级token,请检查前端网络及apiServer地址。
          _that.form.verifyToken = token;
        },
        fail: () => {
          _that.form.verifyToken = '';
        }
      });
    }
  },
};
</script>


